import React from 'react';
import './Task.css'

function Task({index, text, isComplete, isShared, onTaskStatus, onRemove}) {
    return (
        <div className="c-task" >
            {isComplete?
                <img index={index} src={process.env.PUBLIC_URL+"/done.jpg"}  onClick={(e) => onTaskStatus(e)} className="complete"/>
                
                :<div index={index}  className="empty" onClick={(e) => onTaskStatus(e) }></div>}
            <p className={isComplete?"h4 line-through":"h4"}>{text}</p>
            <div index={index} className="is-hidden">
                <div className="img-container"> <img className="share" alt="share" src="data:image/png;base64,"></img>
                </div>
                <div className="img-container"> <img className="edit" alt="edit" src="data:image/png;base64,"></img>
                </div>
                <div index={index} className="img-container" onClick={(e) => onRemove(e)} > <img index={index} onClick={(e) => onRemove(e)}  className="remove" alt="remove" src="data:image/svg+xml;base64,"/>
                </div>
            </div>
        </div>
    );
}

export default Task;